.nav {
  margin-bottom: 50px;
  
  display: flex;
  justify-content: center;
}

.link {
  color: black;
  font-weight: 700;
  font-size: 24px;
  text-decoration: none;
  
  transition: color 0.3s ease-in;
}

.link:hover {
  color: blue;
}

.link:not(:last-child) {
  margin-right: 40px;
}